<template>
  <container :title="title">
    <list :editRoute="arctypeModRoute" :show-del="true">
      <thead slot="thead">
      <tr>
        <th><checkbox v-model="checked" :checked="tgCheck"/></th>
        <th>{{lang.id}}</th>
        <th>{{lang.name}}</th>
        <th>{{lang.operation}}</th>
        <th>{{lang.status}}</th>
        <th>{{lang.sorts}}</th>
        <th width="125">{{$t('operation.name')}}</th>
      </tr>
      </thead>
      <tbody slot="tbody">
        <tr v-for="vol in list">
          <td><checkbox  v-model="checkbox" :checked="checked" :value="vol.id"/></td>
          <td>{{vol.id}}</td>
          <td>{{vol.name}}</td>
          <td>{{vol.operation}}</td>
          <v-td type="status" :value="vol.status" />
          <td>{{vol.sorts}}</td>
          <v-td type="opt" :value="vol.id"
                edit-route="article_model_create_edit" />
        </tr>
      </tbody>
    </list>
  </container>
</template>

<script>
  import {Container,List, VTd} from "@/components"
  import Checkbox from "../../components/forms/Checkbox"

  export default {

    components: {Checkbox, VTd,Container, List},
    data(){
      return{
        title:this.$t('arctype_mod.index'),
        lang:this.$t('arctype_mod'),
        arctypeModRoute:{
          name:'article_model_create_edit'
        },
        checked:false,
        tgCheck:false,
      }
    },
    watch:{
      checkbox(newValue){
        const length = newValue.length
        if (length === this.list.length){
          this.tgCheck = true
        }
        if (length === 0) {
          this.tgCheck = false
        }
      }
    },
    computed:{
      list(){
        return this.$store.state.table.list
      },
      checkbox:{
        get(){
          return this.$store.state.table.checkbox
        },
        set(value){
          this.$store.commit('table/SET_CHECKBOX',value)
        }
      },
    },
    mounted(){
      const url = '/api/arctype/model/index'
      this.$store.commit('table/SET_URL', url)
      this.$store.dispatch('table/fetchList')
    }
  }
</script>



